import {mapState} from 'vuex';
export default {

	data() {
		return {
			swiperCurrent: 0,
			swiperLength: 0
		};
	},

	onLoad() {
		this.swiperLength = this.carouselList.length;
	},
	computed:{
		...mapState({
			// 轮播图
			carouselList:state => state.orgIntroduction.carouselList
		})
	},
	methods: {
		//轮播图切换
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			
		},
		//学校简介
		navToOrgIntroductionPage() {
			uni.navigateTo({
			    url: '../orgIntroduction/orgIntroduction?flag='+'content',
			    animationType: 'pop-in',
			    animationDuration: 500
			});
		},
		//招生简章
		navToOrgEmploymentPage() {
			uni.navigateTo({
			    url: '../orgIntroduction/orgIntroduction?flag='+'employment',
			    animationType: 'pop-in',
			    animationDuration: 500
			});
		},
		//本周食谱
		navToRecipePage() {
			uni.navigateTo({
			    url: '../recipe/recipe?flag='+'employment',
			    animationType: 'pop-in',
			    animationDuration: 500
			});
		},
	},
}
